@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
.Box{padding:1rem;margin-bottom:30px;background-color:#fff;border-radius:12px;box-shadow:0px 4px 10px 4px rgba(0,0,0,.02);overflow:hidden}
.Login{height:100vh;display:flex;align-items:center;justify-content:center;flex-direction:row}.Login_container{width:400px;padding:50px 40px !important;display:flex;align-items:center;justify-content:center;flex-direction:column}.Login_logo{width:200px;margin-bottom:40px}
.Sidebar{height:100%;position:fixed !important;left:0;background-color:#fff}.Sidebar .ant-layout-sider-zero-width-trigger{display:flex;align-items:center;justify-content:center;flex-direction:row;width:30px;height:30px;top:34px;right:-28px;box-shadow:0px 4px 10px 4px rgba(0,0,0,.02)}.Sidebar .ant-layout-sider-children{display:flex;align-items:stretch;justify-content:stretch;flex-direction:column;height:100%;padding:30px 30px 50px 30px;overflow:auto}.Sidebar__collapsed .ant-layout-sider-children{padding:0 !important}.Sidebar_logo{width:200px;display:block;margin-left:auto;margin-right:auto}.Sidebar_sidebarList{margin-top:20px}.Sidebar_sidebarList_item{position:relative;display:flex;align-items:center;justify-content:stretch;flex-direction:row;padding:10px 0;margin:15px 0;transition:300ms}.Sidebar_sidebarList_item__active .Sidebar_sidebarList_item_name{color:#e0bc5d;font-weight:bold}.Sidebar_sidebarList_item_icon{margin-right:15px}.Sidebar_sidebarList_item_name{font-family:"Nunito",sans-serif;font-weight:600;font-size:16px;line-height:100%;color:#626b77}.Sidebar_sidebarList_item_itemCount{display:flex;align-items:center;justify-content:center;flex-direction:row;width:20px;height:20px;position:absolute;right:0;border-radius:50%;background-color:#d45340;font-size:.65rem;color:#fff}.Sidebar .bottom{position:relative;width:100%;margin-top:auto}.Sidebar .bottom.active>.menu{display:block}.Sidebar .bottom .user-details{display:flex;align-items:center;justify-content:stretch;flex-direction:row;width:100%;padding:10px 15px;border-radius:16px;background-color:#f5f6fa;cursor:pointer}.Sidebar .bottom .user-details .avatar{width:40px;height:40px;border-radius:50%;margin-right:15px;border:2px solid #ff8f3f}.Sidebar .bottom .user-details .user-text-info{flex:1 1}.Sidebar .bottom .user-details .user-text-info .name{display:block;width:130px;font-family:"Nunito",sans-serif;font-weight:bold;font-size:17px;line-height:100%;color:#2d3845;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.Sidebar .bottom .user-details .user-text-info .role{width:130px;display:block;margin-top:3px;font-family:"Nunito",sans-serif;font-weight:500;font-size:15px;line-height:100%;color:#ff8f3f;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.Sidebar .bottom .menu{width:100%;padding:10px 15px;margin-top:auto;border-radius:16px;background-color:#f5f6fa;position:absolute;display:none;bottom:70px}.Sidebar .bottom .menu .item{position:relative;display:flex;align-items:center;justify-content:stretch;flex-direction:row;padding:12px 0;transition:300ms;cursor:pointer}.Sidebar .bottom .menu .item .icon{margin-right:15px}.Sidebar .bottom .menu .item .icon.icon-active{display:none}.Sidebar .bottom .menu .item .name{font-family:"Nunito",sans-serif;font-weight:600;font-size:16px;line-height:100%;color:#2d3845}
.ant-layout{background:transparent !important}.Container_spinner{width:100%}
.ContentLayout{min-height:100vh !important;margin-left:280px;width:calc(100% - 281px);overflow-x:hidden}@media only screen and (max-width: 768px){.ContentLayout{width:100% !important}}.ContentLayout__sidebarCollapsed{margin-left:0 !important}.ContentLayout_header{height:auto !important;width:100%;padding:35px 30px 30px 30px !important;background:transparent !important}.ContentLayout_header_title{display:flex;align-items:center;justify-content:flex-start;flex-direction:row;font-family:"Nunito",sans-serif;font-weight:800;font-size:1.875rem;line-height:100%;margin:0}.ContentLayout_mainContent{padding:0 30px}
.Branches_createRow{margin-bottom:20px}
.Deliveries_createRow{margin-bottom:20px}.CreateDelivery_grandTotal{margin-top:20px;text-align:right}.ViewDelivery_setAsDelivered{margin-top:20px}.ViewDelivery_print{margin-bottom:20px}
.BranchProducts_productName{padding:0 !important}
.Preorders_createRow{margin-bottom:20px}.ViewPreorder_setPreorderAsComplete{margin-bottom:20px}.ViewPreorder_setPreorderAsComplete_button{margin-left:auto;display:block !important}@media(max-width: 599px){.ViewPreorder_setPreorderAsComplete button{display:block;width:100%;margin:unset}}.ViewPreorder_transaction_createRow{margin-bottom:20px}
.Products_createRow{margin-bottom:20px}.Products_productName{padding:0 !important}.Products_filter{margin-bottom:20px}.ModifyProduct_btnAddPrice{display:block !important;margin-left:auto;margin-right:auto}.ModifyProduct_btnSubmit{padding-left:30px !important;padding-right:30px !important}
html,body{max-width:100%;overflow-x:hidden}body{background-color:#f5f6fa;font-family:"Nunito",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}
